      }
  },
  methods: {
    addUser(item) {
      console.log('222222')
      this.loginUserList[item.socket_id] = item
    },
    test() {
      this.$axios.get('/users/myinfo').then(function(result) {
        console.log(result.data);
      });
    },

    test2() {
      this.$axios.get('/auth/test').then(function(result) {
        console.log(result.data);
      });
    }
  }
}
</script>



